  document.getElementById('signUpButton').addEventListener('click', function (event) {
    // if (!recaptchaResponse) {
    //   return;
    // }
    const currentBtn = event.currentTarget;

    let responseData = {};

    const { email, emailInputWrap, emailErrorText } = getSignEmailValidationElements();
    const { password, passwordInputWrap, passErrorText } = getSignPasswordValidationElements();

    const isEmailValid = validateForm(email, '', emailInputWrap, emailErrorText, null, null);
    const isPasswordValid = validateForm(
      '',
      password,
      null,
      null,
      passwordInputWrap,
      passErrorText
    );

    if (!isEmailValid) {
      window.dataLayer.push({
        event: 'auth_error_email',
        order_error_type: emailErrorText.innerText,
      });
    }

    if (!isPasswordValid) {
      window.dataLayer.push({
        event: 'auth_error_password',
        order_error_type: passErrorText.innerText,
      });
    }

    if (!isEmailValid || !isPasswordValid) return;

    toggleLoader(currentBtn, true);
    fetch('https://app.paperwriters24.com/api/auth/signup/', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        email,
        password,
        role: 'customer',
        client_id: 'pgnfidgwuKhlfrh4jRuIirRpvteChDGnga9I4hmj',
        // g_recaptcha_response: recaptchaResponse,
        site: 'app.paperwriters24.com',
        is_premium_blog_user: true,
      }),
      credentials: 'include',
    })
      .then((response) => response.json())
      .then((data) => {
        responseData = data;

        const userId = getCookie('extra_metadata');
        setCookie({ name: 'accessToken', value: data.access_token, domain: '.paperwriters24.com' });
        setCookie({
          name: 'refreshToken',
          value: data.refresh_token,
          domain: '.paperwriters24.com',
        });

        if (!data.error_map) {
          return fetchUserData();
        }
        if (typeof data.message === 'object' && data.message !== null) {
          window.dataLayer.push({
            event: 'auth_error_email',
            order_error_type: data.message.email[0] || '',
          });
        } else {
          window.dataLayer.push({
            event: 'auth_error_password',
            order_error_type: data.message,
          });
        }
      })
      .then((userData) => {
        const combinedData = {
          ...responseData,
          ...userData,
        };

        if (combinedData.new_user) {
          setCookie({ name: 'new_user', value: 'true', domain: '.paperwriters24.com' });
          userSignUpEvent(combinedData.id, 'email');
        } else {
          userLoginEvent(combinedData.id, 'email');
        }

        handleApiResponse(
          combinedData,
          emailInputWrap,
          passwordInputWrap,
          passErrorText,
          emailErrorText
        );
      })
      .catch((error) => console.error('Error:', error))
      .finally(() => {
        toggleLoader(currentBtn, false);
      });
  });

