  // === AUTH EVENTS ===
  function userLoginEvent(userId, method) {
    window.dataLayer.push({
      event: 'user_login',
      user_id: userId,
      method,
      color_theme: 'system',
    });
  }

  function userSignUpEvent(userId, method) {
    const currentDate = new Date();
    const formattedDate = `${currentDate.getDate()}.${
      currentDate.getMonth() + 1
    }.${currentDate.getFullYear()}`;

    window.dataLayer.push({
      event: 'CompleteRegistration',
      eventCategory: 'SignUp',
      eventAction: 'Completed',
      userId,
      registration_date: formattedDate,
      method,
      color_theme: 'system',
    });
  }

  function userMethodRegisterEvent(eventName) {
    const activeTab = document.querySelector('.auth-form__tabs__link.w--current');
    const clickText = activeTab ? activeTab.textContent.trim() : '';

    window.dataLayer.push({
      event: eventName,
      click_text: clickText,
      type: 'Auth modal',
    });
  }

  // === UNIVERSAL PUSH ===
  function pushEvent(event, buttonText, clickText, type) {
    window.dataLayer.push({
      event,
      button_text: buttonText || undefined,
      click_text: clickText,
      type: type || undefined,
      subject: typeof selectedSubject !== 'undefined' ? selectedSubject : null,
    });
  }

