  // === INIT ===
  buttonsConfig.forEach(({ selector, index, event, buttonText, clickText, type }) => {
    const elements = document.querySelectorAll(selector);
    if (elements.length === 0) return;

    if (typeof index === 'number' && elements[index]) {
      elements[index].addEventListener('click', () =>
        pushEvent(event, buttonText, clickText, type)
      );
    } else {
      elements.forEach((el) =>
        el.addEventListener('click', () => pushEvent(event, buttonText, clickText, type))
      );
    }
  });

